.header-wrapper {
    height: 300px;
    background: #1c1c1c;
    width: 100%;
}

.header-wrapper .profile-pic {
    border-radius: 50%;
    margin: 50px;
    width: 200px;
    height: 200px;
    float: left;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.header-wrapper .profile-content {
    float: left;
    margin-top: 50px;
    color: #afc0d4;
}

.header-wrapper .contact {
    float: right;
    color: #afc0d4;
    margin-right: 50px;
    margin-top: 50px;
}

.socialMedia-list {
    padding-left: 0;
}

.socialMedia-list li {
    list-style: none;
    display: inline-block;
    margin-right: 10px;
}

.socialMedia-list .fa {
    color: #1c1c1c;
    font-size: 20px;
    padding-top: 8px;
}

.socialMedia-list a {
    background-color: #afc0d4;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    display: inline-block;
    text-align: center;
}